<!--  -->
<template>
  <div class="">
    <el-table
      :data="flowData"
      height="650"
      border
      style="width: 100%; margin-left: 15px"
      :header-cell-style="{
        'background-color': 'rgba(32, 39, 65, 0.7)',
        color: '#fff',
      }"
    >
      <el-table-column type="index" min-width="10%" align="center">
      </el-table-column>
      <el-table-column prop="priority" label="优先级"> </el-table-column>
      <el-table-column prop="cookie" label="cookie"> </el-table-column>
      <el-table-column prop="idle_timeout" label="idle_timeout">
      </el-table-column>
      <el-table-column prop="hard_timeout" label="hard_timeout">
      </el-table-column>
      <el-table-column prop="byte_count" label="byte_count"> </el-table-column>
      <el-table-column prop="packet_count" label="packet_count">
      </el-table-column>
      <el-table-column prop="length" label="length"> </el-table-column>
      <el-table-column prop="actions" label="actions"> </el-table-column>
      <el-table-column prop="table_id" label="table_id"> </el-table-column>
      <el-table-column label="match">
        <template slot-scope="scope">
          <el-button type="text" @click="test(scope.$index, scope.row)"
            >展开</el-button
          >
          <el-dialog title="match" :visible.sync="dialogVisible" width="30%">
            <el-descriptions>
              <el-descriptions-item label="dl_type">{{
                match.dl_type
              }}</el-descriptions-item>
              <el-descriptions-item label="源地址">{{
                match.nw_src
              }}</el-descriptions-item>
              <el-descriptions-item label="目的地址">{{
                match.nw_dst
              }}</el-descriptions-item>
              <el-descriptions-item label="nw_proto">
                <el-tag size="medium">{{ match.nw_proto }}</el-tag>
              </el-descriptions-item>
              <el-descriptions-item label="tp_src">{{
                match.tp_src
              }}</el-descriptions-item>
              <el-descriptions-item label="tp_des">{{
                match.tp_des
              }}</el-descriptions-item>
            </el-descriptions>
          </el-dialog>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    //这里存放数据
    return {
      flowData: [],
      dialogVisible: false,
      match: [],
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    loadFlows() {
      this.$axios
        .get(this.$httpUrl + "/flow/information")
        .then((res) => res.data)
        .then((res) => {
          console.log(res);
          this.flowData = res;
        });
    },
    handleClose(done) {
      this.$confirm("确认关闭？")
        // eslint-disable-next-line
        .then((_) => {
          done();
        })
        // eslint-disable-next-line
        .catch((_) => {});
    },
    test(a, b) {
      console.log(a);
      console.log(b);
      this.match = b.match;
      this.dialogVisible = true;
    },
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {
    this.loadFlows();
  },
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
<style  lang="scss" scoped>
</style>